<template>
  <v-container>
    <v-card>
      <v-card-title>
        <h3 class="mx-auto">Varsity</h3>
      </v-card-title>
      <v-card-subtitle>
        <p class="text-center">Exams</p>
      </v-card-subtitle>
      <v-card-text>
        <div class="subject__filters my-3">
          <button
            :class="`subject__button ${se == '' && 'activebtn'}`"
            @click="selectSubject('')"
          >
            All
          </button>
          <button
            v-for="(s, i) in subjects"
            :key="i"
            :class="`subject__button ${se == s && 'activebtn'}`"
            @click="selectSubject(s)"
          >
            {{ s }}
          </button>
        </div>
        <div class="subject__filters">
          <v-sheet>
            <v-text-field
              label="Search..."
              append-icon="mdi-magnify"
              v-model="search"
            ></v-text-field
          ></v-sheet>
        </div>
        <v-data-table
          :items-per-page="20"
          :items-per-page-options="[20, 50, 100, -1]"
          :search="search"
          :items="selected"
          :headers="headers"
          :loading="selected.length < 1"
          class="suleman black--text"
        >
          <template v-slot:item.link="{ item }">
            <v-btn
              :href="item.link"
              small
              target="_blank"
              class="indigo"
              fab
              dark
              v-if="item.link"
            >
              <v-icon>mdi-play</v-icon>
            </v-btn>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      routine: [],
      selected: [],
      subjects: new Set(),

      search: "",
      se: "",
      headers: [
        { text: "Chapter", value: "chapter" },
        { text: "Subject", value: "subject", align: "" },
        { text: "Question", value: "link", align: "center" },
      ],
    };
  },
  methods: {
    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbwkXvx0yzjiRrEiRBLUiFDqqpw2sU6W-Mv9aH_QncMgo_xaviSmqtij-odnni1wtGX1/exec?type=varsityexams"
      )
        .then((res) => res.json())
        .then((data) => {
          this.routine = data.routines;
          this.selected = this.routine;
          this.routine.forEach((r) => {
            if (r.subject) {
              let s = r.subject
                .replace(" 1st Paper", "")
                .replace(" 2nd Paper", "");
              this.subjects.add(s);
            }
          });
        });
    },
    selectSubject(s) {
      this.se = s;
      this.selected = this.routine.filter((r) => {
        if (r.subject.includes(s)) {
          return r;
        }
      });
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
.title__text {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .main_heading {
    font-size: 20px !important;
  }
}
.subject__button {
  background: rgba(209, 52, 4, 0.897);
  margin-left: 5px;
  padding: 10px 15px;
  color: white;
  border-radius: 5px;
  box-shadow: 1px 1px 2px rgba($color: #18192413, $alpha: 1);
}
.activebtn {
  background: rgba($color: #121118, $alpha: 1);
  box-shadow: 2px 2px 4px rgba($color: #aeaeb969, $alpha: 0.9) !important;
}
.subject__filters {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: center;
  align-content: center;
}
</style>